import React, { useContext } from "react";
import { FormattedMessage } from "react-intl";
import { Link, useLocation } from "react-router-dom";
import { GiTwoCoins } from "react-icons/gi";
import Particles from "react-particles-js";

import { useActiveWeb3React } from "../../web3";
import { formatAddress, formatAmount } from "../../utils/format";
import { mainContext } from "../../reducer";
import { HANDLE_WALLET_MODAL, HANDLE_SHOW_MENUMASK_MODAL } from "../../const";
import LogoText from "../../assets/logo/logo.png";
import { ReactComponent as More } from "../../assets/icon/more.svg";
import { Banner } from "../banner/Banner";
import { useBalance } from "../../pages/Hooks";

export const Header = () => {
  const { active, account } = useActiveWeb3React();
  const { dispatch } = useContext(mainContext);
  const { balance } = useBalance("0x0");

  const location = useLocation();
  return (
    <header
      className="header"
      style={location.pathname === "/" ? { borderBottom: "transparent" } : {}}
    >
      <Particles
        params={{
          particles: {
            number: {
              value: 60,
              density: {
                enable: true,
                value_area: 1500,
              },
            },
            line_linked: {
              enable: true,
              opacity: 0.02,
            },
            move: {
              direction: "right",
              speed: 0.05,
            },
            size: {
              value: 1,
            },
            opacity: {
              anim: {
                enable: true,
                speed: 1,
                opacity_min: 0.05,
              },
            },
          },
          interactivity: {
            events: {
              onclick: {
                enable: true,
                mode: "push",
              },
            },
            modes: {
              push: {
                particles_nb: 1,
              },
            },
          },
          retina_detect: true,
        }}
      />
      <div className="header_wrapper">
        <div className="center">
          <div className="header__box">
            <Link to="/" className="header__logo">
              <img src={LogoText} className="anypad-logo" alt="" />
            </Link>

            <Link
              to="/"
              className={`header__logo--small ${active ? "active" : ""}`}
            >
              <img src={LogoText} className="anypad-logo" alt="" />
            </Link>

            <div className="header__menu">
              <nav className="menu">
                <ul className="menu__list">
                  <li className="menu__item">
                    <a target="_blank" rel="noopener noreferrer" href="https://anypad.io/">Home</a>
                  </li>
                </ul>
              </nav>
            </div>

            <div className="header__menu-wrapper">
              {active && (
                <div className="ht-balance">
                  <GiTwoCoins style={{ "--color": "white" }} />
                  <p style={{ marginLeft: 4 }}>{formatAmount(balance)}</p>
                </div>
              )}
              {active && (
                <div className="header-account">
                  <div
                    className="address"
                    onClick={() => {
                      dispatch({
                        type: HANDLE_WALLET_MODAL,
                        walletModal: "status",
                      });
                    }}
                  >
                    {formatAddress(account)}
                  </div>
                </div>
              )}

              {!active && (
                <div className="header__btn">
                  <button className="connect-btn">
                    <span
                      onClick={() => {
                        dispatch({
                          type: HANDLE_WALLET_MODAL,
                          walletModal: "connect",
                        });
                      }}
                    >
                      <FormattedMessage id="linkWallet" />
                    </span>
                  </button>
                </div>
              )}
            </div>
            <div className="more">
              <More
                onClick={() => {
                  dispatch({
                    type: HANDLE_SHOW_MENUMASK_MODAL,
                    showMenuMaskModal: true,
                  });
                }}
              />
            </div>
          </div>
        </div>
      </div>
      <Banner />
    </header>
  );
};
