import React, { useEffect, useState } from 'react'

import { usePoolsInfo } from '../../pages/pools/Hooks'
import BSCImage from '../../assets/image/bsc.png';

export const Banner = () => {
  const pools = usePoolsInfo()
  const [statistics, setStatistics] = useState({
    raised: 0,
    paticipants: 0
  });

  useEffect(() => {
    fetch('statistics.json').then(res => res.json())
    .then(data => setStatistics(data))
    .catch((e) => e);
  }, []);

  return (
    <div className='banner'>
      <div style={{ textAlign: 'center' }}>
        <div className='banner_text_img'>
          <div className='banner_dec'>
            <div className="first-intro">
              <p>First Decentralized</p>
              <p><span className="highlight">Multi-Chain</span> IDO LaunchPad +</p>
              <p>Multi-Chain <span className="highlight">AMM DEX</span></p>
            </div>
            <div className="secord-intro">
              <p>AnyPad launchpad will allow creators to launch their tokens most innovative crypto projects across all blockchains</p>
              <p>AnyPad DEX will plug into multiple liquidity sources across chains, including the cross-chain liquidity ecosystem. </p>
            </div>
            <div>
              <p className="liveOn">
                Live on
                <div>
                  <img src={BSCImage} alt="" style={{ width: 200 }} />
                </div>
                <div>
                  <img style={{ width: 146 }} src="data:image/png;base64," alt="" />
                </div>
              </p>
            </div>
          </div>
        </div>
      </div>
      <div className="statistic">
        <div className="title">Statistics</div>
        <div className="statistic-container">
          <div className="statistic-label">Total Funds Raised</div>
          <div className="statistic-value">${statistics.raised}</div>
        </div>
        <div className="statistic-container">
          <div className="statistic-label">All-time Unique Paticipants</div>
          <div className="statistic-value">{statistics.paticipants}</div>
        </div>
        <div className="statistic-container">
          <div className="statistic-label">Projects lauched</div>
          <div className="statistic-value">{pools.length}</div>
        </div>
      </div>
    </div>
  )
}
