import React, { memo } from "react";
import { Link } from "react-router-dom";
import { formatUnits } from "@ethersproject/units";
import Timer from "react-compound-timer";

import useStakePool from "../../pages/stake-pool/useStakePool";
import IcGithub from "../icons/IcGithub";
import IcTelegram from "../icons/IcTelegram";
import IcTwitter from "../icons/IcTwitter";
import IcWebsite from "../icons/IcWebsite";

function StakePoolItem({ data }) {
  const { pool } = useStakePool(data.address);
  console.log(pool);
  return (
    <div className="box" style={{ padding: "18px", display: "inline-block" }}>
      <div className="pools-type_card_box pools-type_private">
        <div className="pools-type_title">
          <p className="pools-type_card_title">Stake pool</p>
          <p className="pools-type_card_title_right">
            {pool.status === "waiting" && Number(pool.stakingStartTime) > 0 && (
              <span className="pools-type_time">
                <Timer
                  initialTime={
                    Number(pool.stakingStartTime) * 1000 - +new Date()
                  }
                  key={Number(pool.stakingStartTime)}
                  direction="backward"
                  formatValue={(number) => {
                    if (number === 0) return "00";
                    if (number < 10) {
                      return `0${number}`;
                    }
                    return number;
                  }}
                >
                  <span>
                    <Timer.Consumer>
                      {({ h, d, formatValue }) => formatValue(d * 24 + h)}
                    </Timer.Consumer>
                  </span>
                  &nbsp;:&nbsp;
                  <span>
                    <Timer.Minutes />
                  </span>
                  &nbsp;:&nbsp;
                  <span>
                    <Timer.Seconds />
                  </span>
                </Timer>
              </span>
            )}
            {pool.status === "inprogress" &&
              Number(pool.purchasingEndTime) > 0 && (
                <span className="pools-type_time">
                  <Timer
                    initialTime={
                      Number(pool.purchasingEndTime) * 1000 - +new Date()
                    }
                    key={Number(pool.purchasingEndTime)}
                    direction="backward"
                    formatValue={(number) => {
                      if (number === 0) return "00";
                      if (number < 10) {
                        return `0${number}`;
                      }
                      return number;
                    }}
                  >
                    <span>
                      <Timer.Consumer>
                        {({ h, d, formatValue }) => formatValue(d * 24 + h)}
                      </Timer.Consumer>
                    </span>
                    &nbsp;:&nbsp;
                    <span>
                      <Timer.Minutes />
                    </span>
                    &nbsp;:&nbsp;
                    <span>
                      <Timer.Seconds />
                    </span>
                  </Timer>
                </span>
              )}
          </p>
        </div>
        <div style={{ paddingTop: 20, paddingBottom: 20 }}>
          {pool && pool.icon ? (
            <img
              src={pool.icon}
              alt=""
              style={{
                height: 120,
                margin: "0 auto",
              }}
            />
          ) : null}
        </div>
        <div className="pool-contents">
          <div className="pool-flex">
            <div className="pool-label">
              Status
              <span className="pool-value">
                <span className="pool_status">
                  {pool ? pool.status || "-" : "-"}
                </span>
              </span>
            </div>
            <div className="pool-label">
              Total allocation
              <span className="pool-value">
                {pool
                  ? formatUnits(
                      pool.totalAllocation || "0",
                      pool.currencyTokenInfo && pool.currencyTokenInfo.decimals
                        ? Number(pool.currencyTokenInfo.decimals)
                        : 0
                    )
                  : 0}
                &nbsp;
                {pool && pool.currencyTokenInfo
                  ? pool.currencyTokenInfo.symbol
                  : ""}
              </span>
            </div>
            <div className="pool-label">
              Total staked
              <span className="pool-value">
                {pool
                  ? formatUnits(
                      pool.totalStaking || "0",
                      pool.stakingTokenInfo && pool.stakingTokenInfo.decimals
                        ? Number(pool.stakingTokenInfo.decimals)
                        : 0
                    )
                  : 0}
                &nbsp;
                {pool && pool.stakingTokenInfo
                  ? pool.stakingTokenInfo.symbol
                  : ""}
              </span>
            </div>
            <div className="pool-label">
              Type
              <span className="pool-value pool-access-text">Stake pool</span>
            </div>
          </div>
        </div>
        <div className="pool-sharing">
          {pool && pool.website && (
            <a href={pool.website} target="_blank" rel="noopener noreferrer">
              <IcWebsite />
            </a>
          )}
          {pool && pool.twitter && (
            <a href={pool.twitter} target="_blank" rel="noopener noreferrer">
              <IcTwitter />
            </a>
          )}
          {pool && pool.Telegram_Channel && (
            <a
              href={pool.Telegram_Channel}
              target="_blank"
              rel="noopener noreferrer"
            >
              <IcTelegram />
            </a>
          )}
          {pool && pool.Github && (
            <a href={pool.Github} target="_blank" rel="noopener noreferrer">
              <IcGithub />
            </a>
          )}
        </div>
        <Link to={`/pools/stake/${data.address}`} className="pools-type_enter">
          View pool
        </Link>
      </div>
    </div>
  );
}

export default memo(StakePoolItem);
