import React, { useContext } from 'react'
import { FormattedMessage } from 'react-intl'
import { BiCopy } from 'react-icons/bi'
import { AiOutlineUserSwitch } from 'react-icons/ai'

import {
  GALLERY_SELECT_WEB3_CONTEXT,
  HANDLE_WALLET_MODAL,
} from '../../const'
import { mainContext } from '../../reducer'
import close from '../../assets/icon/close.png'
import { formatAddress } from '../../utils/format'
import switchIcon from '../../assets/icon/switch.png'
import { CopyToClipboard } from 'react-copy-to-clipboard'
import { useWeb3React } from '@web3-react/core'

export const WalletModal = ({ onChange, onClose }) => {
  const context = useWeb3React()
  const { dispatch } = useContext(mainContext)

  const {
    account,
    deactivate,
  } = context

  return (
    <div className='modal'>
      <div className='modal__box'>
        <form className='form-app' action='/'>
          <div className='form-app__inner transction-submitted'>
            <div
              className='form-app__inner__header'
              style={{ maxWidth: 'inherit' }}
            >
              Your address
            </div>
            <p className='form-app__inner__address'>
              {account && formatAddress(account)}
            </p>

            <CopyToClipboard
              text={account}
              onCopy={() => {
                alert('copy success!')
              }}
            >
              <div
                className='form-app__inner__frame'
              >
                <BiCopy />
                <p style={{ marginLeft: 5 }}>
                  Copy address
                </p>
              </div>
            </CopyToClipboard>

            <div className='form-app__inner__frame' onClick={onChange}>
              <AiOutlineUserSwitch />
              <p style={{ marginLeft: 5 }}>
                Switch wallet
              </p>
            </div>

            <button
              style={{ marginTop: 30, width: '100%' }}
              type='button'
              className='transction-submitted__btn btn'
              onClick={() => {
                deactivate()
                dispatch({
                  type: HANDLE_WALLET_MODAL,
                  walletModal: null,
                })
                window &&
                  window.localStorage.removeItem(GALLERY_SELECT_WEB3_CONTEXT)
              }}
            >
              <FormattedMessage id='modalsText62' />
            </button>
            <img
              src={close}
              alt=''
              className='form-app__close-btn'
              onClick={onClose}
              aria-label='Close'
            />
          </div>
        </form>
      </div>
    </div>
  )
}
