/* eslint-disable react-hooks/exhaustive-deps */
import { useState, useEffect } from 'react'
import BigNumber from 'bignumber.js'

import { getContract, getLogs, useActiveWeb3React } from '../../web3'
import { abi as ERC20 } from '../../web3/abi/ERC20.json'
import Pools from '../../configs/pools'
import Web3 from 'web3'
import { formatAmount, fromWei, numToWei } from '../../utils/format'

export function useBlockHeight() {
  const { library } = useActiveWeb3React()
  const [blockNumber, setBlockNumber] = useState(0)

  const updateBlockNumber = (blockNumber) => {
    setBlockNumber(blockNumber)
  }

  useEffect(() => {
    library && library.once('block', updateBlockNumber)
    return () => {
      library && library.off('block', updateBlockNumber)
    }
  }, [blockNumber, library])

  return blockNumber
}

export const usePoolsInfo = (address = '') => {
  const { account, library, chainId } = useActiveWeb3React()
  const blockHeight = useBlockHeight()

  const now = parseInt(Date.now() / 1000)

  let pools = Pools.filter((o) => address === '' || o.address === address);

  if (address === '') {
    pools = pools.filter((o) => chainId && o.chainId === chainId);
  }

  const [poolsInfo, setPoolsInfo] = useState(pools)

  pools.map((item) => {
    let status = item.status
    if (status === 0) {
      status = now < item.start_at ? 0 : now < item.time ? 1 : 2
    }

    if (item.type === 2) {
      return item;
    };

    return Object.assign(item, {
      quotaOf: 0,
      status: status,
      timeClose: item.timeClose || '0',
      totalPurchasedUnderlying:
        status === 3 ? Web3.utils.toWei(item.amount) : 0,
      currency: {
        use_native_token: item.currency.address === '0x0',
        ...item.currency,
      },
    })
  })

  useEffect(() => {
    if (!chainId) return;
    const p = Pools.filter((o) => address === '' || o.address === address).filter((o) => chainId && o.chainId === chainId);
    const newPools = p.map((item) => {
      let status = item.status
      if (status === 0) {
        status = now < item.start_at ? 0 : now < item.time ? 1 : 2
      }

      if (item.type === 2) {
        return item;
      };
  
      return Object.assign(item, {
        quotaOf: 0,
        status: status,
        timeClose: item.timeClose || '0',
        totalPurchasedUnderlying:
          status === 3 ? Web3.utils.toWei(item.amount) : 0,
        currency: {
          use_native_token: item.currency.address === '0x0',
          ...item.currency,
        },
      })
    });
    setPoolsInfo(newPools);
  }, [chainId]);

  useEffect(() => {
    if (library) {
      Promise.all(
        pools.map((pool) => {
          if (pool.is_coming) return pool

          const currency_token = pool.currency && pool.currency.use_native_token
            ? null
            : getContract(library, ERC20, (pool.currency || {}).address)
          if (pool.type === 0) {
            const pool_contract = getContract(library, pool.abi, pool.address)
            const promise_list = [
              pool_contract.methods.purchaseDeadline
                ? pool_contract.methods.purchaseDeadline().call()
                : 0,
              pool_contract.methods.settleTime
                ? pool_contract.methods.settleTime().call()
                : 0,
              pool_contract.methods.price().call(),
              pool_contract.methods.totalPurchasedCurrency().call(),
              pool_contract.methods.purchasedCurrencyOf(account).call(),
              pool_contract.methods.totalSettleable().call(),
              pool_contract.methods.settleable(account).call(),
              getLogs(library, pool.abi, {
                address: pool.address,
                topics: [null, Web3.utils.padLeft(account, 64)],
              }),
              currency_token
                ? currency_token.methods.allowance(account, pool.address).call()
                : 0,
              pool_contract.methods.totalSettledUnderlying().call(),
            ]
            return Promise.all(promise_list).then(
              ([
                time,
                timeSettle,
                price,
                totalPurchasedCurrency,
                purchasedCurrencyOf,
                totalSettleable,
                settleable,
                logs,
                currency_allowance,
                totalSettledUnderlying,
              ]) => {
                let status = pool.status || 0;
                const timeClose = time
                if (timeSettle) {
                  time = timeSettle
                }
                if (pool.start_at < now && status < 1) {
                  status = 1
                }
                if (time < now && status < 2) {
                  status = 2
                }

                if (
                  totalSettleable.volume == totalSettledUnderlying &&
                  totalSettleable.volume > 0
                ) {
                  status = 3
                }
                // const totalPurchasedAmount = new BigNumber(
                //   Web3.utils.toWei(pool.amount, 'ether')
                // )
                //   .multipliedBy(new BigNumber(price))
                //   .div(new BigNumber(Web3.utils.toWei('1', 'ether')))

                const totalPurchasedUnderlying = Web3.utils.toWei(
                  new BigNumber(totalPurchasedCurrency)
                    .dividedBy(new BigNumber(price))
                    .toFixed(0, 1),
                  'ether'
                )

                let is_join = false
                if (purchasedCurrencyOf > 0) {
                  is_join = true
                }

                Object.assign(pool.currency, {
                  allowance: currency_allowance,
                })

                return Object.assign({}, pool, {
                  ratio: `1 ${pool.underlying.symbol} = ${formatAmount(
                    price,
                    pool.underlying.decimal,
                    8
                  )}${pool.currency.symbol}`,
                  progress:
                    new BigNumber(totalPurchasedUnderlying)
                      .div(numToWei(pool.amount, pool.underlying.decimal))
                      .toFixed(4)
                      .toString() * 1,
                  status: status,
                  time: time,
                  timeClose,
                  price: Web3.utils.fromWei(price, 'ether'),
                  is_join,
                  totalPurchasedCurrency,
                  totalPurchasedAmount: totalPurchasedCurrency,
                  totalPurchasedUnderlying,
                  purchasedCurrencyOf,
                  totalSettleable,
                  totalSettledUnderlying,
                  settleable,
                  logs,
                })
              }
            )
          } else if (pool.type === 1) {
            let currency_decimals = pool.currency.decimal

            const underlying_token = getContract(
              library,
              ERC20,
              pool.underlying.address
            )
            const currency_token = pool.currency.use_native_token
            ? null
            : getContract(library, ERC20, pool.currency.address)

            const pool_contract = getContract(library, pool.abi, pool.address)
            const promise_list = [
              pool_contract.methods.purchaseTime().call(),
              pool_contract.methods.withdrawTime().call(),
              pool_contract.methods.ratio().call(),
              pool_contract.methods.totalAllocation().call(),
              pool_contract.methods.totalPurchased().call(),
              pool_contract.methods.totalClaimed().call(),
              pool_contract.methods.allocationOf(account).call(),
              pool_contract.methods.purchasedOf(account).call(),
              pool_contract.methods.claimedOf(account).call(),
              underlying_token.methods.decimals().call(),
              currency_token
                ? currency_token.methods.allowance(account, pool.address).call()
                : 0,
            ]
            return Promise.all(promise_list).then(
              ([
                start_at,
                time,
                ratio,
                totalQuota,
                totalOffered,
                totalClaimed,
                quotaOf,
                offeredOf,
                claimedOf,
                underlying_decimals,
                currency_allowance,
              ]) => {
                let status = pool.status || 0;
                if (start_at < now && status < 1) {
                  status = 1
                }

                if (time < now && status < 2) {
                  status = 2
                }

                if (
                  status === 2 &&
                  totalOffered === totalClaimed &&
                  totalClaimed > 0
                ) {
                  status = 3
                }
                if (!(ratio - 0) && pool.defaultRatio) {
                  ratio = pool.defaultRatio
                }
                const _ratio = new BigNumber(ratio).dividedBy(
                  new BigNumber(10).pow(
                    parseInt(underlying_decimals) -
                      parseInt(currency_decimals) +
                      18
                  )
                )
                // const __ratio2 = new BigNumber(1).dividedBy(
                //   new BigNumber(ratio).dividedBy(
                //     new BigNumber(10).pow(
                //       parseInt(underlying_decimals) -
                //         parseInt(currency_decimals) +
                //         18
                //     )
                //   )
                // )
                const __ratio = new BigNumber(1).dividedBy(
                  new BigNumber(ratio).dividedBy(
                    new BigNumber(10).pow(
                      parseInt(underlying_decimals)
                    )
                  )
                )
                // console.log(__ratio.toString())
                // const totalPurchasedAmount = Web3.utils.toWei(
                //   new BigNumber(pool.amount)
                //     .dividedBy(new BigNumber(__ratio))
                //     .toFixed(6, 1)
                //     .toString(),
                //   'ether'
                // )
                const totalPurchasedAmount = new BigNumber(fromWei(totalOffered, pool.underlying.decimal)).multipliedBy(__ratio).times(
                  new BigNumber(10).pow(pool.currency.decimal)
                );
                const totalPurchasedCurrency = Web3.utils.toWei(
                  new BigNumber(Web3.utils.fromWei(totalOffered, 'ether'))
                    .dividedBy(new BigNumber(_ratio))
                    .toFixed(6, 1)
                    .toString(),
                  'ether'
                )

                let is_join = false
                if (offeredOf > 0) {
                  is_join = true
                }

                Object.assign(pool.currency, {
                  allowance: currency_allowance,
                })

                return Object.assign({}, pool, {
                  ratio: `1 ${pool.underlying.symbol} = ${
                    __ratio.toFixed(4, 1).toString() * 1
                  }${pool.currency.symbol}`,
                  progress:
                    new BigNumber(Web3.utils.fromWei(totalOffered, 'ether'))
                      .dividedBy(new BigNumber(pool.amount))
                      .toNumber()
                      .toFixed(2) * 1,
                  status: status,
                  start_at,
                  time: time,
                  timeClose: 0,
                  price:
                    new BigNumber(Web3.utils.toWei('1', 'ether'))
                      .dividedBy(new BigNumber(ratio))
                      .toFixed(6, 1) * 1,
                  is_join,
                  totalPurchasedCurrency,
                  totalPurchasedAmount,
                  totalPurchasedUnderlying: totalOffered,
                  purchasedCurrencyOf: Web3.utils.toWei(
                    new BigNumber(Web3.utils.fromWei(offeredOf, 'ether'))
                      .dividedBy(new BigNumber(_ratio))
                      .toFixed(6, 1)
                      .toString(),
                    'ether'
                  ),
                  quotaOf,
                  totalSettleable: {
                    amount: 0,
                    volume: totalClaimed,
                    rate: Web3.utils.toWei('1', 'ether'),
                  },
                  settleable: {
                    amount: 0,
                    volume: offeredOf,
                    claimedOf,
                    rate: Web3.utils.toWei('1', 'ether'),
                  },
                })
              }
            )
          } else if (pool.type === 2) {
            return Object.assign({}, pool);
          }
        })
      ).then((pools) => {
        setPoolsInfo(pools)
      })
    }

    return () => {}
  }, [account, address, blockHeight])
  return poolsInfo
}
