import React, { useCallback, useState } from "react";
import { message, Modal } from "antd";
import { useRouteMatch } from "react-router-dom";
import { formatUnits } from "@ethersproject/units";
import Timer from "react-compound-timer";

import PoolsHeader from "../../components/staterPools/poolsHeader";
import StakeForm from "./StakeForm";
import PurchaseForm from "./PurchaseForm";
import useStakePool from "./useStakePool";
import { getContract, useActiveWeb3React } from "../../web3";
import IcWebsite from "../../components/icons/IcWebsite";
import IcTwitter from "../../components/icons/IcTwitter";
import IcTelegram from "../../components/icons/IcTelegram";
import IcGithub from "../../components/icons/IcGithub";

const StakePoolAbi = require("./StakePoolAbi.json");

export default function StakePoolDetail() {
  const [stakeForm, setStakeForm] = useState(false);
  const [claimLoading, setClaimLoading] = useState(false);
  const [purchaseForm, setPurchaseForm] = useState(false);
  const router = useRouteMatch();
  const { pool } = useStakePool(router.params.address);
  const { library, account } = useActiveWeb3React();

  const claim = useCallback(() => {
    if (!library || !account) return;
    (async () => {
      try {
        setClaimLoading(true);
        const contract = getContract(
          library,
          StakePoolAbi,
          router.params.address
        );
        await contract.methods.unstakeAndWithdraw().send({
          from: account,
        });
      } catch (e) {
        message.error(e.message || e.toString());
      } finally {
        setClaimLoading(false);
      }
    })();
  }, [library, account, router.params.address]);

  const closeStakeForm = () => setStakeForm(false);
  const openStakeForm = () => setStakeForm(true);
  const closePurchaseForm = () => setPurchaseForm(false);
  const openPurchaseForm = () => setPurchaseForm(true);
  return (
    <div>
      <Modal
        visible={stakeForm}
        onCancel={closeStakeForm}
        title="Stake token"
        maskClosable={false}
        footer={null}
        destroyOnClose
      >
        <StakeForm
          address={router.params.address}
          tokenAddress={pool ? pool.stakingToken : undefined}
          onClose={closeStakeForm}
        />
      </Modal>
      <Modal
        visible={purchaseForm}
        onCancel={closePurchaseForm}
        title="Confirm purchase"
        maskClosable={false}
        footer={null}
        destroyOnClose
      >
        <PurchaseForm
          address={router.params.address}
          tokenAddress={pool ? pool.currencyToken : undefined}
          purchaseByNativeToken={pool ? pool.purchaseByNativeToken : false}
          onClose={closePurchaseForm}
          allocationOf={pool ? pool.allocationOf : undefined}
          tokenInfo={pool ? pool.currencyTokenInfo : undefined}
          volumeOf={pool ? pool.volumeOf : undefined}
          underlyingTokenInfo={pool ? pool.underlyingTokenInfo : undefined}
        />
      </Modal>
      <PoolsHeader />
      <div className="pools-container">
        <div className="pools-information">
          <div className="pools-item-container">
            <div className="pools-item-label">Allocation</div>
            <div className="pools-item-value">
              {pool
                ? formatUnits(
                    pool.totalAllocation || "0",
                    pool.currencyTokenInfo && pool.currencyTokenInfo.decimals
                      ? Number(pool.currencyTokenInfo.decimals)
                      : 0
                  )
                : 0}
              &nbsp;
              {pool && pool.currencyTokenInfo
                ? pool.currencyTokenInfo.symbol
                : ""}
            </div>
          </div>
          <div className="pools-item-container">
            <div className="pools-item-label">Total staked token</div>
            <div className="pools-item-value">
              {pool
                ? formatUnits(
                    pool.totalStaking || "0",
                    pool.stakingTokenInfo && pool.stakingTokenInfo.decimals
                      ? Number(pool.stakingTokenInfo.decimals)
                      : 0
                  )
                : 0}
              &nbsp;
              {pool && pool.stakingTokenInfo
                ? pool.stakingTokenInfo.symbol
                : ""}
            </div>
          </div>
          <div className="pools-item-container">
            <div className="pools-item-label">Token sell name</div>
            <div className="pools-item-value">
              {pool && pool.underlyingTokenInfo
                ? pool.underlyingTokenInfo.name || ""
                : "-"}
            </div>
          </div>
          <div className="pools-item-container">
            <div className="pools-item-label">Token sell address</div>
            <div className="pools-item-value address">
              {pool && pool.underlyingToken ? pool.underlyingToken : "-"}
            </div>
          </div>
          <div className="pools-item-container">
            <div className="pools-item-label">Token stake name</div>
            <div className="pools-item-value">
              {pool && pool.stakingTokenInfo
                ? pool.stakingTokenInfo.name || ""
                : "-"}
            </div>
          </div>
          <div className="pools-item-container">
            <div className="pools-item-label">Token stake address</div>
            <div className="pools-item-value address">
              {pool ? pool.stakingToken : "-"}
            </div>
          </div>
          <div className="pools-item-container">
            <div className="pools-item-label">Token buy</div>
            <div className="pools-item-value">
              {pool && pool.currencyTokenInfo
                ? pool.currencyTokenInfo.name || ""
                : "-"}
            </div>
          </div>
          <div className="pools-item-container">
            <div className="pools-item-label">Type</div>
            <div className="pools-item-value private">Stake pool</div>
          </div>
          <div className="pools-sharing">
            {pool && pool.website && (
              <a href={pool.website} target="_blank" rel="noopener noreferrer">
                <IcWebsite />
              </a>
            )}
            {pool && pool.twitter && (
              <a href={pool.twitter} target="_blank" rel="noopener noreferrer">
                <IcTwitter />
              </a>
            )}
            {pool && pool.Telegram_Channel && (
              <a
                href={pool.Telegram_Channel}
                target="_blank"
                rel="noopener noreferrer"
              >
                <IcTelegram />
              </a>
            )}
            {pool && pool.Github && (
              <a href={pool.Github} target="_blank" rel="noopener noreferrer">
                <IcGithub />
              </a>
            )}
          </div>
        </div>
        <div className="pools-joined">
          <div style={{ paddingTop: 20, paddingBottom: 20 }}>
            {pool && pool.icon ? (
              <img
                src={pool.icon}
                alt=""
                style={{
                  height: 120,
                  margin: "0 auto",
                }}
              />
            ) : null}
          </div>
          <div className="pools-item-container">
            <div className="pools-item-label">Your allocation</div>
            <div className="pools-item-value address">
              {pool
                ? formatUnits(
                    pool.allocationOf || "0",
                    pool.currencyTokenInfo && pool.currencyTokenInfo.decimals
                      ? Number(pool.currencyTokenInfo.decimals)
                      : 0
                  )
                : 0}
              &nbsp;
              {pool && pool.currencyTokenInfo
                ? pool.currencyTokenInfo.symbol
                : ""}
            </div>
          </div>
          <div className="pools-item-container">
            <div className="pools-item-label">Your stake</div>
            <div className="pools-item-value address">
              {pool
                ? formatUnits(
                    pool.stakingOf || "0",
                    pool.stakingTokenInfo && pool.stakingTokenInfo.decimals
                      ? Number(pool.stakingTokenInfo.decimals)
                      : 0
                  )
                : 0}
              &nbsp;
              {pool && pool.stakingTokenInfo
                ? pool.stakingTokenInfo.symbol
                : ""}
            </div>
          </div>
          <div className="pools-item-container">
            <div className="pools-item-label">Your purchased</div>
            <div className="pools-item-value address">
              {pool
                ? formatUnits(
                    pool.purchasedVolumeOf || "0",
                    pool.underlyingTokenInfo &&
                      pool.underlyingTokenInfo.decimals
                      ? Number(pool.underlyingTokenInfo.decimals)
                      : 0
                  )
                : 0}
              &nbsp;
              {pool && pool.underlyingTokenInfo
                ? pool.underlyingTokenInfo.symbol
                : ""}
            </div>
          </div>
          <div className="pools-item-container">
            <div className="pools-item-label">Status</div>
            <div className="pools-item-value">
              <div className="comming">{pool ? pool.status || "-" : "-"}</div>
            </div>
          </div>
          {pool &&
            (pool.status === "waiting" ||
              pool.status === "inprogress" ||
              pool.status === "staking") && (
              <div className="pools-item-container">
                <div className="pools-item-label">Time left</div>
                <div className="pools-item-value">
                  <div>
                    {pool.status === "waiting" &&
                      Number(pool.stakingStartTime) > 0 && (
                        <span className="pools-type_time">
                          <Timer
                            initialTime={
                              Number(pool.stakingStartTime) * 1000 - +new Date()
                            }
                            key={Number(pool.stakingStartTime)}
                            direction="backward"
                            formatValue={(number) => {
                              if (number === 0) return "00";
                              if (number < 10) {
                                return `0${number}`;
                              }
                              return number;
                            }}
                          >
                            <span>
                              <Timer.Consumer>
                                {({ h, d, formatValue }) =>
                                  formatValue(d * 24 + h)
                                }
                              </Timer.Consumer>
                            </span>
                            &nbsp;:&nbsp;
                            <span>
                              <Timer.Minutes />
                            </span>
                            &nbsp;:&nbsp;
                            <span>
                              <Timer.Seconds />
                            </span>
                          </Timer>
                        </span>
                      )}
                    {pool.status === "staking" &&
                      Number(pool.purchasingStartTime) > 0 && (
                        <span className="pools-type_time">
                          <Timer
                            initialTime={
                              Number(pool.purchasingStartTime) * 1000 -
                              +new Date()
                            }
                            key={Number(pool.purchasingStartTime)}
                            direction="backward"
                            formatValue={(number) => {
                              if (number === 0) return "00";
                              if (number < 10) {
                                return `0${number}`;
                              }
                              return number;
                            }}
                          >
                            <span>
                              <Timer.Consumer>
                                {({ h, d, formatValue }) =>
                                  formatValue(d * 24 + h)
                                }
                              </Timer.Consumer>
                            </span>
                            &nbsp;:&nbsp;
                            <span>
                              <Timer.Minutes />
                            </span>
                            &nbsp;:&nbsp;
                            <span>
                              <Timer.Seconds />
                            </span>
                          </Timer>
                        </span>
                      )}
                    {pool.status === "inprogress" &&
                      Number(pool.purchasingEndTime) > 0 && (
                        <span className="pools-type_time">
                          <Timer
                            initialTime={
                              Number(pool.purchasingEndTime) * 1000 -
                              +new Date()
                            }
                            key={Number(pool.purchasingEndTime)}
                            direction="backward"
                            formatValue={(number) => {
                              if (number === 0) return "00";
                              if (number < 10) {
                                return `0${number}`;
                              }
                              return number;
                            }}
                          >
                            <span>
                              <Timer.Consumer>
                                {({ h, d, formatValue }) =>
                                  formatValue(d * 24 + h)
                                }
                              </Timer.Consumer>
                            </span>
                            &nbsp;:&nbsp;
                            <span>
                              <Timer.Minutes />
                            </span>
                            &nbsp;:&nbsp;
                            <span>
                              <Timer.Seconds />
                            </span>
                          </Timer>
                        </span>
                      )}
                  </div>
                </div>
              </div>
            )}
          <div className="pools-buttons">
            <div className="btn-wrapper">
              {pool && pool.status && (
                <>
                  {pool.status === "waiting" && (
                    <div className="pools-btn join disabled">
                      Waiting for pool open
                    </div>
                  )}
                  {pool.status === "staking" && (
                    <div
                      className="pools-btn join active"
                      onClick={openStakeForm}
                    >
                      Stake token
                    </div>
                  )}
                  {pool.status === "inprogress" &&
                    Number(pool.purchasedCurrencyOf) === 0 &&
                    Number(pool.stakingOf) > 0 && (
                      <div
                        className="pools-btn join active"
                        onClick={openPurchaseForm}
                      >
                        Purchase
                      </div>
                    )}
                  {pool.status === "ended" && (
                    <>
                      {pool.stakingOf && Number(pool.stakingOf) > 0 ? (
                        <div
                          className={`pools-btn join ${
                            claimLoading ? "disabled" : "active"
                          }`}
                          onClick={claim}
                        >
                          {claimLoading ? "Loading..." : "Unstake and claim"}
                        </div>
                      ) : (
                        <div className="pools-btn join disabled">
                          This pool has been ended
                        </div>
                      )}
                    </>
                  )}
                </>
              )}
              <a
                className="pools-btn heco"
                href={`https://bscscan.com/address/${router.params.address}`}
                target="_blank"
                rel="noopener noreferrer"
              >
                View on Binance Smart Chain
              </a>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
