import React, { useContext, useEffect, useState } from 'react'
import { formatAmount, fromWei, numToWei } from '../../utils/format'
import { getRandomIntInclusive } from '../../utils/index'
import { useBalance } from '../../pages/Hooks'
import Web3 from 'web3'
import { getContract, useActiveWeb3React } from '../../web3'
import { injectIntl } from 'react-intl'
import ERC20 from '../../web3/abi/ERC20.json'
import { FormattedMessage } from 'react-intl'

import {
  HANDLE_SHOW_FAILED_TRANSACTION_MODAL,
  HANDLE_SHOW_TRANSACTION_MODAL,
  HANDLE_SHOW_WAITING_WALLET_CONFIRM_MODAL,
  waitingForInit,
  waitingPending,
} from '../../const'
import { mainContext } from '../../reducer'
import BigNumber from 'bignumber.js'

const PoolsJoin = (props) => {
  const { intl, onClose, pool } = props
  const { account, library } = useActiveWeb3React()
  const { dispatch } = useContext(mainContext)
  const [approve, setApprove] = useState(true)
  const [amount, setAmount] = useState('')
  const [maxAllocation, setMaxAllocation] = useState();
  const [purchased, setPurchased] = useState();
  const [fee, setFee] = useState(0)

  const currency_address = pool ? pool.currency.address : '0x0'
  const { balance = 0 } = useBalance(currency_address)

  useEffect(() => {
    const gas_limit = new BigNumber('1006182')
    const gas_price = new BigNumber(Web3.utils.toWei(`${getRandomIntInclusive(5, 20)}`, 'gwei'))
    const _fee =  gas_limit.multipliedBy(gas_price).toString()
    setFee(_fee)
  }, [])

  useEffect(() => {
    if (pool && (Number(pool.currency.allowance) > 0 || pool.currency.use_native_token)) {
      setApprove(false)
    }
  }, [pool])

  useEffect(() => {
    if (pool && pool.currency.use_native_token) {
      const poolContract = getContract(library, pool.abi, pool.address);
      (async () => {
        try {
          const [maxAllc, purc] = await Promise.all([
            poolContract.methods.maxAllocation().call(),
            poolContract.methods.purchasedCurrencyOf(account).call(),
          ]);
          setMaxAllocation(maxAllc);
          setPurchased(purc);
        } catch (e) {
          //
        }
      })();
    }
  }, [pool, library, account]);

  const onMax = () => {
    let max = balance
    const maxB = new BigNumber(max)
    const balanceB = new BigNumber(balance)
    const quotaOfB = new BigNumber(pool.quotaOf)
    if (pool.type === 1 && quotaOfB.lt(balanceB)) {
      max = pool.quotaOf
    }

    if (pool.type === 0) {
      const numBl = Number(fromWei(max, 18).toString()); // work on bsc and heco chain, it's native token
      const numMaxAll = Number(fromWei(maxAllocation, pool.currency.decimal).toString());
      const numPurc = Number(fromWei(purchased, pool.currency.decimal).toString())

      if (numBl >= numMaxAll) {
        max = maxAllocation;
      }
      if (numPurc > 0 && numPurc < numMaxAll) {
        max = numToWei(fromWei(maxAllocation, pool.currency.decimal).minus(fromWei(purchased, pool.currency.decimal)).toString(), pool.currency.decimal);
      }
    }

    if(pool.currency.use_native_token && max === balance){
      const feeB = new BigNumber(fee)
      max = maxB.gt(feeB) ? maxB.minus(feeB).toString() : 0
    }

    setAmount(formatAmount(max, pool.currency.decimal, 6))
  }

  const onChange = (e) => {
    const { value } = e.target;
    const re = /^[0-9]+([.|,][0-9]+)?$/g;
    if (
        value === '' ||
        re.test(value) ||
        (value.split('.').length === 2 &&
            value.slice(value.length - 1) === '.')
    ) {
      setAmount(value);
    }
  };

  const onApprove = (e) => {
    const contract = getContract(library, ERC20.abi, pool.currency.address)
    contract.methods
      .approve(
        pool.address,
        '0xffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffff'
      )
      .send({
        from: account,
      })
      .on('transactionHash', (hash) => {
        dispatch({
          type: HANDLE_SHOW_WAITING_WALLET_CONFIRM_MODAL,
          showWaitingWalletConfirmModal: { ...waitingPending, hash },
        })
      })
      .on('receipt', (_, receipt) => {
        console.log('approve success')
        dispatch({
          type: HANDLE_SHOW_WAITING_WALLET_CONFIRM_MODAL,
          showWaitingWalletConfirmModal: waitingForInit,
        })
        dispatch({
          type: HANDLE_SHOW_TRANSACTION_MODAL,
          showTransactionModal: true,
        })
        setApprove(false)
      })
      .on('error', (err, receipt) => {
        console.log('approve error', err)
        dispatch({
          type: HANDLE_SHOW_FAILED_TRANSACTION_MODAL,
          showFailedTransactionModal: true,
        })
        dispatch({
          type: HANDLE_SHOW_WAITING_WALLET_CONFIRM_MODAL,
          showWaitingWalletConfirmModal: waitingForInit,
        })
      })
  }

  const onConfirm = (e) => {
    if (!amount) {
      return false
    }
    if (isNaN(parseInt(amount))) {
      return false
    }
    if (pool.type === 1) {
      const pool_contract = getContract(library, pool.abi, pool.address)
      if(pool.currency.use_native_token){
        pool_contract.methods
            .purchaseNative()
            .send({
              from: account,
              value: Web3.utils.toWei(`${amount}`, 'ether'),
            })
            .on('transactionHash', (hash) => {
              dispatch({
                type: HANDLE_SHOW_WAITING_WALLET_CONFIRM_MODAL,
                showWaitingWalletConfirmModal: { ...waitingPending, hash },
              })
            })
            .on('receipt', (_, receipt) => {
              console.log('BOT staking success')
              dispatch({
                type: HANDLE_SHOW_WAITING_WALLET_CONFIRM_MODAL,
                showWaitingWalletConfirmModal: waitingForInit,
              })
              dispatch({
                type: HANDLE_SHOW_TRANSACTION_MODAL,
                showTransactionModal: true,
              })
            })
            .on('error', (err, receipt) => {
              console.log('BOT staking error', err)
              dispatch({
                type: HANDLE_SHOW_FAILED_TRANSACTION_MODAL,
                showFailedTransactionModal: true,
              })
              dispatch({
                type: HANDLE_SHOW_WAITING_WALLET_CONFIRM_MODAL,
                showWaitingWalletConfirmModal: waitingForInit,
              })
            })
      }else {
        const _amount = numToWei(amount, pool.currency.decimal)
        pool_contract.methods
            .purchase(_amount)
            .send({
              from: account
            })
            .on('transactionHash', (hash) => {
              dispatch({
                type: HANDLE_SHOW_WAITING_WALLET_CONFIRM_MODAL,
                showWaitingWalletConfirmModal: { ...waitingPending, hash },
              })
            })
            .on('receipt', (_, receipt) => {
              console.log('BOT staking success')
              dispatch({
                type: HANDLE_SHOW_WAITING_WALLET_CONFIRM_MODAL,
                showWaitingWalletConfirmModal: waitingForInit,
              })
              dispatch({
                type: HANDLE_SHOW_TRANSACTION_MODAL,
                showTransactionModal: true,
              })
            })
            .on('error', (err, receipt) => {
              console.log('BOT staking error', err)
              dispatch({
                type: HANDLE_SHOW_FAILED_TRANSACTION_MODAL,
                showFailedTransactionModal: true,
              })
              dispatch({
                type: HANDLE_SHOW_WAITING_WALLET_CONFIRM_MODAL,
                showWaitingWalletConfirmModal: waitingForInit,
              })
            })
      }

    } else {

      const pool_contract = getContract(library, pool.abi, pool.address)
      const _amount = numToWei(amount, pool.currency.decimal)

      if(pool.currency.use_native_token){
        pool_contract.methods
            .purchaseNative()
            .send({
              from: account,
              value: _amount
            })
            .on('transactionHash', (hash) => {
              dispatch({
                type: HANDLE_SHOW_WAITING_WALLET_CONFIRM_MODAL,
                showWaitingWalletConfirmModal: { ...waitingPending, hash },
              })
            })
            .on('receipt', (_, receipt) => {
              console.log('BOT staking success')
              dispatch({
                type: HANDLE_SHOW_WAITING_WALLET_CONFIRM_MODAL,
                showWaitingWalletConfirmModal: waitingForInit,
              })
              dispatch({
                type: HANDLE_SHOW_TRANSACTION_MODAL,
                showTransactionModal: true,
              })
            })
            .on('error', (err, receipt) => {
              console.log('BOT staking error', err)
              dispatch({
                type: HANDLE_SHOW_FAILED_TRANSACTION_MODAL,
                showFailedTransactionModal: true,
              })
              dispatch({
                type: HANDLE_SHOW_WAITING_WALLET_CONFIRM_MODAL,
                showWaitingWalletConfirmModal: waitingForInit,
              })
            })
      }else{
        pool_contract.methods
            .purchase(_amount)
            .send({
              from: account,
            })
            .on('transactionHash', (hash) => {
              dispatch({
                type: HANDLE_SHOW_WAITING_WALLET_CONFIRM_MODAL,
                showWaitingWalletConfirmModal: { ...waitingPending, hash },
              })
            })
            .on('receipt', (_, receipt) => {
              console.log('BOT staking success')
              dispatch({
                type: HANDLE_SHOW_WAITING_WALLET_CONFIRM_MODAL,
                showWaitingWalletConfirmModal: waitingForInit,
              })
              dispatch({
                type: HANDLE_SHOW_TRANSACTION_MODAL,
                showTransactionModal: true,
              })
            })
            .on('error', (err, receipt) => {
              console.log('BOT staking error', err)
              dispatch({
                type: HANDLE_SHOW_FAILED_TRANSACTION_MODAL,
                showFailedTransactionModal: true,
              })
              dispatch({
                type: HANDLE_SHOW_WAITING_WALLET_CONFIRM_MODAL,
                showWaitingWalletConfirmModal: waitingForInit,
              })
            })
      }

    }
    onClose()
  }

  return (
    <div className='modal'>
      <div className='modal__box'>
        <form className='form-app' action='/'>
          <div className='form-app__inner deposit'>
            <h1
              className='form-app__title h3'
              style={{ marginTop: 0, marginBottom: '10px' }}
            >
              <FormattedMessage id='poolText16' />
              {pool && pool.underlying.symbol}
              <FormattedMessage id='poolText17' />
            </h1>
            <p className='form-app__tip'>{pool && pool.ratio}</p>
            <p
              className='form-app__inputbox-after-text'
              style={{
                marginBottom: 0,
                textAlign: 'left',
                opacity: 1,
              }}
            >
              <FormattedMessage id='poolText18' />
              {balance && pool && ` ${formatAmount(balance, pool.currency.decimal)} ${pool.currency.symbol}`}
              <br />
            </p>
            <p
              className='form-app__inputbox-after-text'
              style={{
                marginBottom: 0,
                textAlign: 'left',
                opacity: 1,
              }}
            >
              Max purchase: {maxAllocation && pool && ` ${formatAmount(maxAllocation, pool.currency.decimal)} ${pool.currency.symbol}`}
              <br />
            </p>
            <p
              className='form-app__inputbox-after-text'
              style={{
                marginBottom: 0,
                textAlign: 'left',
                opacity: 1,
              }}
            >
              Your purchased: {purchased && pool && ` ${formatAmount(purchased,pool.currency.decimal)} ${pool.currency.symbol}`}
            </p>
            {pool && pool.type === 1 && (
              <p
                className='form-app__inputbox-after-text'
                style={{
                  marginBottom: 0,
                  textAlign: 'left',
                  opacity: 1,
                }}
              >
                Your quota:
                {pool &&
                  pool.type === 1 &&
                  pool.quotaOf > 0 &&
                  ` ${formatAmount(pool.quotaOf, pool.currency.decimal)} ${pool.currency.symbol}`}
                <br />
              </p>
            )}

            <div className='deposit__inputbox form-app__inputbox'>
              <div className='form-app__inputbox-control'>
                <div className='form-app__inputbox-input'>
                  <input
                    value={amount}
                    onChange={onChange}
                    className='input'
                    placeholder={intl.formatMessage({
                      id: 'money',
                    })}
                  />
                </div>

                <div className='form-app__inputbox-up' onClick={onMax}>
                  <div className='form-app__inputbox-up-pref'>
                    <FormattedMessage id='poolText19' />
                  </div>
                </div>
              </div>
            </div>

            <div className='form-app__submit form-app__submit--row'>
              <button
                className='btn btn--outline btn--medium'
                type='button'
                onClick={onClose}
              >
                <FormattedMessage id='poolText20' />
              </button>
              {approve ? (
                <button
                  type='button'
                  className='btn btn--medium'
                  onClick={onApprove}
                >
                  <FormattedMessage id='poolText21' />
                </button>
              ) : (
                <button
                  type='button'
                  className='btn btn--medium'
                  onClick={onConfirm}
                >
                  <FormattedMessage id='poolText22' />
                </button>
              )}
            </div>
          </div>
        </form>
      </div>
    </div>
  )
}

export default injectIntl(PoolsJoin)
