import { useEffect, useState } from "react";
import { getContract, useActiveWeb3React } from "../../web3";

const abi = require("../../web3/abi/ERC20.json");

export default function useTokenInfo(address, poolAddress, updateId) {
  const [info, setInfo] = useState(
    address === "0x0000000000000000000000000000000000000000"
      ? {
          name: "BNB",
          symbol: "BNB",
          decimals: 18,
        }
      : {}
  );
  const { library, account } = useActiveWeb3React();

  useEffect(() => {
    if (!library || !address) return;

    (async () => {
      try {
        const contract = getContract(library, abi.abi, address);
        const [name, symbol, decimals, totalSupply] = await Promise.all([
          contract.methods.name().call(),
          contract.methods.symbol().call(),
          contract.methods.decimals().call(),
          contract.methods.totalSupply().call(),
        ]);
        if (poolAddress) {
          const approved = await contract.methods
            .allowance(account, poolAddress)
            .call();
          const balance = await contract.methods
            .balanceOf(account)
            .call();
          setInfo((prev) => ({
            ...prev,
            name,
            symbol,
            decimals,
            totalSupply,
            needToApprove: approved && Number(approved) === 0,
            balance,
          }));
        } else {
          setInfo((prev) => ({
            ...prev,
            name,
            symbol,
            decimals,
            totalSupply,
          }));
        }
      } catch (e) {
        //
      }
    })();
  }, [library, account, poolAddress, address, updateId]);

  return info;
}
