import { useEffect, useState } from "react";
import Web3 from "web3";
import pools from "../../configs/pools";
import { getContract, useActiveWeb3React } from "../../web3";

const stakePoolAbi = require("./StakePoolAbi.json");
const tokenAbi = require("../../web3/abi/ERC20.json");
const BN = Web3.utils.BN;
const ZERO_ADDRESS = "0x0000000000000000000000000000000000000000";

function purchaseByNativeToken(currencyToken) {
  return currencyToken === ZERO_ADDRESS;
}

function getStatus(stakeStartTime, purchaseStartTime, purchaseEndTime) {
  if (!stakeStartTime || !purchaseStartTime || !purchaseEndTime) return null;
  const stakeStart = stakeStartTime * 1000; // to milisec
  const purchaseStart = purchaseStartTime * 1000;
  const purchaseEnd = purchaseEndTime * 1000;
  const now = +new Date();
  if (now < stakeStart) return "waiting";
  if (now >= stakeStart && now < purchaseStart) return "staking";
  if (now >= purchaseStart && now <= purchaseEnd) return "inprogress";
  return "ended";
}

function getProgress(staked, allocation) {
  try {
    const progress = new BN(staked)
      .div(new BN(allocation))
      .mul(new BN(100))
      .toString();
    return Number(progress) > 100 ? 100 : Number(progress);
  } catch (e) {
    return 0;
  }
}

async function getCurrencyTokenInfo(library, address) {
  try {
    if (address === ZERO_ADDRESS) {
      return {
        name: "BNB",
        symbol: "BNB",
        decimals: 18,
      };
    } else {
      const tokenContract = getContract(library, tokenAbi.abi, address);
      const [name, symbol, decimals, totalSupply] = await Promise.all([
        tokenContract.methods.name().call(),
        tokenContract.methods.symbol().call(),
        tokenContract.methods.decimals().call(),
        tokenContract.methods.totalSupply().call(),
      ]);
      return {
        name,
        symbol,
        decimals,
        totalSupply,
      };
    }
  } catch (e) {
    return {
      name: "BNB",
      symbol: "BNB",
      decimals: 18,
    };
  }
}

