async function getUnderlyingTokenInfo(library, address) {
  try {
    if (address === ZERO_ADDRESS) {
      return null;
    } else {
      const tokenContract = getContract(library, tokenAbi.abi, address);
      const [name, symbol, decimals, totalSupply] = await Promise.all([
        tokenContract.methods.name().call(),
        tokenContract.methods.symbol().call(),
        tokenContract.methods.decimals().call(),
        tokenContract.methods.totalSupply().call(),
      ]);
      return {
        name,
        symbol,
        decimals,
        totalSupply,
      };
    }
  } catch (e) {
    return null;
  }
}

function useBlockHeight() {
  const { library } = useActiveWeb3React();
  const [blockNumber, setBlockNumber] = useState(0);

  const updateBlockNumber = (blockNumber) => {
    setBlockNumber(blockNumber);
  };

  useEffect(() => {
    library && library.once("block", updateBlockNumber);
    return () => {
      library && library.off("block", updateBlockNumber);
    };
  }, [blockNumber, library]);

  return blockNumber;
}

export default function useStakePool(address) {
  const initData = pools.filter((p) => p.address === address);
  const [data, setData] = useState(initData && initData.length > 0 ? initData[0] : null);
  const [error, setError] = useState(false);
  const { library, account } = useActiveWeb3React();
  const blockHeight = useBlockHeight();

  useEffect(() => {
    if (!library) return;
    (async () => {
      try {
        const contract = getContract(library, stakePoolAbi, address);
        const poolData = {};
        const calls = [
          contract.methods.currencyToken().call(), // token for buying. If address = zero address -> using bnb (native token) to buy
          contract.methods.underlyingToken().call(), // token sale
          contract.methods.stakingToken().call(), // stake token. Users stake this token to buy underlying token by using currencyToken
          contract.methods.stakingStartTime().call(), // Stake start time
          contract.methods.purchasingStartTime().call(), // Close stake, allow user to buy
          contract.methods.purchasingEndTime().call(), // End buy, allow unstake and claim
          contract.methods.totalAllocation().call(), // total allocation of pool
          contract.methods.totalStaking().call(), // number of token staked
        ];
        const [
          currencyToken,
          underlyingToken,
          stakingToken,
          stakingStartTime,
          purchasingStartTime,
          purchasingEndTime,
          totalAllocation,
          totalStaking,
        ] = await Promise.all(calls);
        poolData.currencyToken = currencyToken;
        poolData.underlyingToken = underlyingToken;
        poolData.stakingToken = stakingToken;
        poolData.stakingStartTime = stakingStartTime;
        poolData.purchasingStartTime = purchasingStartTime;
        poolData.purchasingEndTime = purchasingEndTime;
        poolData.totalAllocation = totalAllocation;
        poolData.totalStaking = totalStaking;
        poolData.purchaseByNativeToken = purchaseByNativeToken(currencyToken);
        poolData.status = getStatus(
          Number(stakingStartTime),
          Number(purchasingStartTime),
          Number(purchasingEndTime)
        );
        poolData.stakingTokenInfo = await getStakeTokenInfo(library, stakingToken);
        poolData.underlyingTokenInfo = await getUnderlyingTokenInfo(library, underlyingToken);
        poolData.currencyTokenInfo = await getCurrencyTokenInfo(library, currencyToken);
        poolData.progress = getProgress(totalStaking, totalAllocation);
        if (account) {
          const calls = [
            contract.methods.stakingOf(account).call(), // number of stake by user
            contract.methods.allocationOf(account).call(), // user allocation
            contract.methods.purchasedCurrencyOf(account).call(), // purchasedCurrencyOf  == 0 && stakingOf > 0 => purchaseable
            contract.methods.purchasedVolumeOf(account).call(),
            contract.methods.volumeOf(account).call(),
          ];
          const [stakingOf, allocationOf, purchasedCurrencyOf, purchasedVolumeOf, volumeOf] = await Promise.all(calls);
          poolData.allocationOf = allocationOf;
          poolData.stakingOf = stakingOf;
          poolData.purchasedCurrencyOf = purchasedCurrencyOf;
          poolData.purchasedVolumeOf = purchasedVolumeOf;
          poolData.volumeOf = volumeOf;
        }
        setData((prev) => ({ ...prev, ...poolData}));
        setError(false);
      } catch (e) {
        setError(true);
      }
    })();
  }, [blockHeight, library, address, account]);

  return {
    pool: data,
    error,
  };
}
