import React, { memo, useCallback, useState } from "react";
import { Input, Button, Form, message } from "antd";
import { parseUnits, formatUnits } from "@ethersproject/units";
import Web3 from "web3";

import { getContract, useActiveWeb3React } from "../../web3";
import useTokenInfo from "./useTokenInfo";

const StakePoolAbi = require("./StakePoolAbi.json");
const Erc20Abi = require("../../web3/abi/ERC20.json");

function StakeForm({ address, tokenAddress, onClose }) {
  const { library, account } = useActiveWeb3React();
  const [updateId, setUpdateId] = useState(1);
  const token = useTokenInfo(tokenAddress, address, updateId);
  const [loading, setLoading] = useState(false);
  const approve = useCallback(() => {
    if (!library || !account || !tokenAddress || !address) return;
    (async () => {
      setLoading(true);
      try {
        const contract = getContract(library, Erc20Abi.abi, tokenAddress);
        await contract.methods
          .approve(address, Web3.utils.toWei("100000000000000"))
          .send({
            from: account,
          });
        message.success("Approve successfully!");
        setUpdateId((prev) => prev + 1);
      } catch (e) {
        message.error(e.message || e.toString());
      } finally {
        setLoading(false);
      }
    })();
  }, [library, account, tokenAddress, address]);
  const stake = useCallback(
    (amount) => {
      if (!library || !account || !amount || !token.decimals) return;
      (async () => {
        setLoading(true);
        try {
          const contract = getContract(library, StakePoolAbi, address);
          await contract.methods
            .stake(parseUnits(amount, token.decimals))
            .send({
              from: account,
            });
          message.success("Stake successfully!");
          onClose && onClose();
        } catch (e) {
          message.error(e.message || e.toString());
        } finally {
          setLoading(false);
        }
      })();
    },
    [library, account, address, token.decimals, onClose]
  );
  const onFinish = (values) => {
    stake(values.amount);
  };
  return (
    <Form
      layout="vertical"
      initialValues={{ layout: "vertical" }}
      onFinish={onFinish}
    >
      <Form.Item label="Amount" name="amount">
        <Input placeholder="Ex: 1000" />
      </Form.Item>
      <div className="token-info">
        <div className="title">Token</div>
        <div className="value">{token ? token.name : '-'}</div>
      </div>
      <div className="token-info">
        <div className="title">Symbol</div>
        <div className="value">{token ? token.symbol : '-'}</div>
      </div>
      <div className="token-info">
        <div className="title">Decimals</div>
        <div className="value">{token ? token.decimals : '-'}</div>
      </div>
      <div className="token-info">
        <div className="title">Your balance</div>
        <div className="value">{token && token.balance ? formatUnits(token.balance, token.decimals) : '-'} {token ? token.symbol : '-'}</div>
      </div>
      <Form.Item>
        {token && token.needToApprove ? (
          <Button type="primary" loading={loading} onClick={approve}>
            Approve
          </Button>
        ) : (
          <Button type="primary" htmlType="submit" loading={loading} disabled={token && token.balance === '0'}>
            Stake
          </Button>
        )}
      </Form.Item>
    </Form>
  );
}

export default memo(StakeForm);
