/* eslint-disable eqeqeq */
/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable default-case */
/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useEffect, useState } from "react";
import cs from "classnames";
import { withRouter } from "react-router";
import { FormattedMessage } from "react-intl";
import { AiOutlineGlobal } from "react-icons/ai";
import { FiTwitter, FiGithub } from "react-icons/fi";
import { FaTelegramPlane } from "react-icons/fa";

import { formatAmount } from "../../utils/format";
import Timer from "react-compound-timer";
import { useActiveWeb3React } from "../../web3";
import { usePoolsInfo } from "./Hooks";
import StakePoolItem from "../../components/stake-pool/StakePoolItem";

const PoolsIndex = (props) => {
  const [listData, setListData] = useState([]);
  const [tabFlag, setTabFlag] = useState(1);
  const [, setIsLogin] = useState(false);
  const [poolSum, setPoolSum] = useState(0);
  const { active } = useActiveWeb3React();

  const pools = usePoolsInfo();

  const _poolsIDO = JSON.parse(JSON.stringify(pools));

  const _pools = [..._poolsIDO];
  const [now, setNow] = useState(parseInt(Date.now() / 1000));

  useEffect(() => {
    const timerId = setTimeout(() => {
      const now = parseInt(Date.now() / 1000);
      setNow(now);
    }, 1000);
    return () => {
      clearTimeout(timerId);
    };
  }, [now]);

  _pools.sort(function (x, y) {
    if (x.status < 3 && y.status < 3) {
      // return x.start_at - y.start_at
      return y.start_at - x.start_at;
    } else {
      return y.start_at - x.start_at;
    }
  });

  const changeTab = (val) => {
    setTabFlag(val);
  };

  const setData = async () => {
    switch (tabFlag) {
      case 1:
        setListData(_pools.filter((o) => o.is_top));
        break;
      case 2:
        setListData(
          _pools.filter(
            (o) =>
              o.is_join ||
              (window.localStorage.is_join && o.underlying.name === "LBP")
          )
        );
        break;
      case 3:
        setListData(_pools.filter((o) => o.is_flash));
        break;
      default:
        setListData(_pools.filter((o) => o.is_top));
    }
  };

  useEffect(() => {
    if (tabFlag !== 1) return;
    setPoolSum(
      listData.filter((o) => {
        return o.is_top && (o.status === 0 || o.status === 1 || o.status === 2);
      }).length
    );
  }, [listData]);

  useEffect(() => {
    setData();
    setIsLogin(active);
  }, [tabFlag, pools, active]);

  const goFinance = (e, flag, url) => {
    e.stopPropagation();
    if (!flag) {
      return;
    }
    window.open(url);
  };

  const goDetail = (e, flag, address, symbol) => {
    e.stopPropagation();
    if (flag) {
      return;
    }
    props.history.push(`/pools/detail/${address}`);
  };

  const renderStatus = (pool) => {
    const { status, timeClose = 0 } = pool;
    switch (status) {
      case 0:
        return (
          <span className="pool_status">
            <FormattedMessage id="willStart" />
          </span>
        );
      case 1:
        if (timeClose === 0 || timeClose > now) {
          return (
            <span className="pool_status">
              <FormattedMessage id="recruit" />
            </span>
          );
        } else {
          return (
            <span className="pool_status">
              <FormattedMessage id="recruitOver" />
            </span>
          );
        }

      case 2:
        return (
          <span className="pool_status">
            <FormattedMessage id="settlement" />
          </span>
        );
      case 3:
        return (
          <span className="pool_status">
            <FormattedMessage id="completed" />
          </span>
        );
    }
  };

  const renderCard = (pool, index) => {
    const {
      address,
      ratio,
      progress,
      status,
      totalPurchasedAmount,
      currency,
      start_at,
      time,
      timeClose,
      type,
    } = pool;
    let left_time = 0;
    if (status === 0) {
      left_time = start_at * 1000 - Date.now();
    } else if (status === 1) {
      if (type === 0) {
        if (now >= timeClose) {
          left_time = (time - now) * 1000;
        } else {
          left_time = (timeClose - now) * 1000;
        }
      } else {
        left_time = (time - now) * 1000;
      }
    }

    return (
      <div
        style={{ padding: "18px", display: "inline-block" }}
        className="box"
        id="pools"
      >
        <div
          className={cs(
            "pools-type_card_box",
            ((pool && pool.underlying.symbol === "FIX") || type === 1) &&
              "pools-type_private",
            pool && pool.is_coming && "pools-type_hover_style",
            tabFlag === 3 && "pools-type_flashPool"
          )}
          onClick={(e) =>
            goFinance(e, pool && pool.is_coming, pool && pool.link_url)
          }
          key={pool.address}
        >
          <div className="pools-type_title">
            <p className="pools-type_card_title">{pool && pool.name}</p>
            <p className="pools-type_card_title_right">
              {status < 3 && left_time > 0 && (
                <span className="pools-type_time">
                  <Timer
                    initialTime={left_time}
                    key={left_time}
                    direction="backward"
                    formatValue={(number) => {
                      if (number === 0) return "00";
                      if (number < 10) {
                        return `0${number}`;
                      }
                      return number;
                    }}
                  >
                    <span>
                      <Timer.Consumer>
                        {({ h, d, formatValue }) => formatValue(d * 24 + h)}
                      </Timer.Consumer>
                    </span>
                    &nbsp;:&nbsp;
                    <span>
                      <Timer.Minutes />
                    </span>
                    &nbsp;:&nbsp;
                    <span>
                      <Timer.Seconds />
                    </span>
                  </Timer>
                </span>
              )}
            </p>
          </div>
          {pool && pool.underlying.name !== "LBP" && (
            <div style={{ paddingTop: 20, paddingBottom: 20 }}>
              <div
                className={`c100 p${Math.floor(
                  pool.progress > 1 ? 100 : pool.progress * 100
                )} blue`}
              >
                <span>{(progress * 100).toFixed(0)}%</span>
                <div className="slice">
                  <div className="bar" />
                  <div className="fill" />
                </div>
              </div>
            </div>
          )}
          <div className="pool-contents">
            <div className="pool-flex">
              <div className="pool-label">
                Status
                <span className="pool-value">{renderStatus(pool)}</span>
              </div>
              <div className="pool-label">
                Pool ratio
                <span className="pool-value">{ratio}</span>
              </div>
              {pool && pool.underlying.name !== "LBP" && (
                <div className="pool-label">
                  Total raised
                  <span className="pool-value">
                    {formatAmount(
                      totalPurchasedAmount,
                      pool.currency.decimal,
                      4
                    )}{" "}
                    {currency.symbol}
                  </span>
                </div>
              )}
              <div className="pool-label">
                Access
                <span className="pool-value pool-access-text">
                  {type === 1 && <FormattedMessage id="private" />}
                  {(type === 0 || type === 2) && (
                    <FormattedMessage id="public" />
                  )}
                </span>
              </div>
            </div>
          </div>
          <div className="pool-sharing">
            {pool.website && pool.website !== "-" && (
              <a href={pool.website} target="_blank" rel="noopener noreferrer">
                <AiOutlineGlobal />
              </a>
            )}
            {pool.twitter && pool.twitter !== "-" && (
              <a href={pool.twitter} target="_blank" rel="noopener noreferrer">
                <FiTwitter />
              </a>
            )}
            {pool.Telegram_Channel && pool.Telegram_Channel !== "-" && (
              <a
                href={pool.Telegram_Channel}
                target="_blank"
                rel="noopener noreferrer"
              >
                <FaTelegramPlane />
              </a>
            )}
            {pool.Github && pool.Github !== "-" && (
              <a href={pool.Github} target="_blank" rel="noopener noreferrer">
                <FiGithub />
              </a>
            )}
          </div>
          <a
            className={cs(
              "pools-type_enter",
              pool && pool.underlying.name === "LBP" && "pools-type_lbp_enter",
              pool &&
                (pool.is_coming ||
                  (status === 3 &&
                    ((pool.type === 0 &&
                      pool.settleable &&
                      pool.settleable.amount == 0 &&
                      pool.settleable.volume == 0) ||
                      (pool.settleable &&
                        pool.type === 1 &&
                        (pool.settleable.claimedOf !== 0 ||
                          pool.settleable.volume == 0)))) ||
                  (!active && status === 3) ||
                  (status === 3 && pool.underlying.name === "LBP")) &&
                "pools-type_disable_enter"
            )}
            onClick={(e) => {
              goDetail(
                e,
                pool &&
                  (pool.is_coming ||
                    (status === 3 &&
                      ((pool.type === 0 &&
                        pool.settleable &&
                        pool.settleable?.amount == 0 &&
                        pool.settleable?.volume == 0) ||
                        (pool.settleable &&
                          pool.type === 1 &&
                          pool.settleable?.claimedOf !== 0) ||
                        pool.settleable?.volume == 0)) ||
                    (!active && status === 3) ||
                    (status === 3 && pool.underlying.name === "LBP")),
                address,
                pool && pool.underlying.name
              );
            }}
          >
            View pool
          </a>
        </div>
      </div>
    );
  };
  const noData = () => {
    return (
      <div className="pools-type_noData">
        <p>
          {(tabFlag === 1 || pools.is_flash) && (
            <FormattedMessage id="noData" />
          )}
          {tabFlag === 2 && <FormattedMessage id="noJoinPool" />}
        </p>
      </div>
    );
  };

  return (
    <div className="pools-type" id="position">
      <div className="pools-type-top">
        <div className="pools-type_content">
          <div className="pools-type_tab">
            <h2
              onClick={() => changeTab(1)}
              className={cs("new_flag", tabFlag === 1 ? "tab_active" : "")}
            >
              Projects ({poolSum || 0})
            </h2>
            <h2
              onClick={() => changeTab(2)}
              className={tabFlag === 2 ? "tab_active" : ""}
            >
              My projects
            </h2>
          </div>
          <div className="pools-type_card">
            {listData &&
              listData.map((pool, index) => {
                if (pool.type === 2) return <StakePoolItem key={pool.address} data={pool} />
                return renderCard(pool, index);
              })}
            {[1, 2].includes(tabFlag) && !listData.length && noData()}
          </div>
        </div>
      </div>
    </div>
  );
};
export default withRouter(PoolsIndex);
