import React, { memo, useCallback, useState } from "react";
import { Button, Form, message } from "antd";
import { formatUnits } from "@ethersproject/units";
import Web3 from "web3";

import { getContract, useActiveWeb3React } from "../../web3";
import useTokenInfo from "./useTokenInfo";

const StakePoolAbi = require("./StakePoolAbi.json");
const Erc20Abi = require("../../web3/abi/ERC20.json");

function PurchaseForm({
  address,
  tokenAddress,
  purchaseByNativeToken,
  onClose,
  allocationOf,
  tokenInfo,
  underlyingTokenInfo,
  volumeOf,
}) {
  const { library, account } = useActiveWeb3React();
  const [updateId, setUpdateId] = useState(1);
  const token = useTokenInfo(tokenAddress, address, updateId);
  const [loading, setLoading] = useState(false);
  const approve = useCallback(() => {
    if (!library || !account || !tokenAddress || !address) return;
    (async () => {
      setLoading(true);
      try {
        const contract = getContract(library, Erc20Abi, tokenAddress);
        await contract.methods
          .approve(address, Web3.utils.toWei("100000000000000"))
          .send({
            from: account,
          });
        message.success("Approve successfully!");
        setUpdateId((prev) => prev + 1);
      } catch (e) {
        message.error(e.message || e.toString());
      } finally {
        setLoading(false);
      }
    })();
  }, [library, account, tokenAddress, address]);
  const purchase = useCallback(() => {
    if (!library || !account) return;
    (async () => {
      setLoading(true);
      try {
        const contract = getContract(library, StakePoolAbi, address);
        await contract.methods.purchase().send({
          from: account,
        });
        message.success("Purchase successfully!");
      } catch (e) {
        message.error(e.message || e.toString());
      } finally {
        setLoading(false);
        onClose && onClose();
      }
    })();
  }, [library, account, address, onClose]);
  const purchaseNative = useCallback(
    (amount) => {
      if (!library || !account || !amount) return;
      (async () => {
        setLoading(true);
        try {
          const contract = getContract(library, StakePoolAbi, address);
          await contract.methods.purchaseNative().send({
            from: account,
            value: amount,
          });
          message.success("Purchase successfully!");
        } catch (e) {
          message.error(e.message || e.toString());
        } finally {
          setLoading(false);
          onClose && onClose();
        }
      })();
    },
    [library, account, address, onClose]
  );
  const onFinish = () => {
    purchaseNative(allocationOf);
  };
  if (!purchaseByNativeToken) {
    return (
      <div>
        <div className="token-info">
          <div className="title">Token</div>
          <div className="value">{token ? token.name : "-"}</div>
        </div>
        <div className="token-info">
          <div className="title">Symbol</div>
          <div className="value">{token ? token.symbol : "-"}</div>
        </div>
        <div className="token-info">
          <div className="title">Decimals</div>
          <div className="value">{token ? token.decimals : "-"}</div>
        </div>
        {allocationOf && tokenInfo && (
          <div className="token-info">
            <div className="title">Your allocation</div>
            <div className="value">
              {formatUnits(allocationOf, token.decimals)} {token.symbol}
            </div>
          </div>
        )}
        {underlyingTokenInfo && (
          <div className="token-info">
            <div className="title">Your volume</div>
            <div className="value">
              {formatUnits(volumeOf || "0", underlyingTokenInfo.decimals)}{" "}
              {underlyingTokenInfo.symbol}
            </div>
          </div>
        )}
        {token && token.needToApprove ? (
          <Button type="primary" loading={loading} onClick={approve}>
            Approve
          </Button>
        ) : (
          <Button
            type="primary"
            loading={loading}
            onClick={purchase}
            disabled={!volumeOf || Number(volumeOf) === 0}
          >
            Confirm purchase
          </Button>
        )}
      </div>
    );
  }
  return (
    <div>
      {allocationOf && tokenInfo && (
        <div>
          <div className="token-info">
            <div className="title">Your allocation</div>
            <div className="value">
              {formatUnits(allocationOf, tokenInfo.decimals)} {tokenInfo.symbol}
            </div>
          </div>
          {underlyingTokenInfo && (
            <div className="token-info">
              <div className="title">Your volume</div>
              <div className="value">
                {formatUnits(volumeOf || "0", underlyingTokenInfo.decimals)}{" "}
                {underlyingTokenInfo.symbol}
              </div>
            </div>
          )}
        </div>
      )}
      <Form.Item>
        <Button
          type="primary"
          onClick={onFinish}
          loading={loading}
          disabled={!volumeOf || Number(volumeOf) === 0}
        >
          Confirm purchase
        </Button>
      </Form.Item>
    </div>
  );
}

export default memo(PurchaseForm);
