/* eslint-disable jsx-a11y/anchor-has-content */
/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useContext, useEffect, useState } from 'react'
import cs from 'classnames'
import { AiOutlineGlobal } from 'react-icons/ai';
import { FiTwitter, FiGithub } from 'react-icons/fi';
import { FaTelegramPlane } from 'react-icons/fa';
import Timer from 'react-compound-timer'
import Web3 from 'web3'
import { FormattedMessage, injectIntl } from 'react-intl'
import BigNumber from 'bignumber.js'

import PoolsHeader from '../../components/staterPools/poolsHeader'
import { HANDLE_WALLET_MODAL } from '../../const'
import { usePoolsInfo } from './Hooks'
import { getContract, useActiveWeb3React } from '../../web3'
import {
  HANDLE_SHOW_FAILED_TRANSACTION_MODAL,
  HANDLE_SHOW_TRANSACTION_MODAL,
  HANDLE_SHOW_WAITING_WALLET_CONFIRM_MODAL,
  waitingForInit,
  waitingPending,
} from '../../const'
import { mainContext } from '../../reducer'
import { formatAmount, fromWei } from '../../utils/format'

const PoolsDetail = (props) => {
  const { address } = props.match.params

  const { intl } = props

  const { account, library, chainId } = useActiveWeb3React()
  const pools = usePoolsInfo(address)

  const [recordTab, setRecordTab] = useState(1)
  const [pool, setPool] = useState(pools[0])
  const [now, setNow] = useState(parseInt(Date.now() / 1000))
  const [left_time, setLeftTime] = useState(0)

  const { dispatch } = useContext(mainContext)

  useEffect(() => {
    const timerId = setTimeout(() => {
      const now = parseInt(Date.now() / 1000)
      setNow(now)
    }, 1000)
    return () => {
      clearTimeout(timerId)
    }
  }, [now]);

  useEffect(() => {
    window.scroll(0, 0);
  }, []);

  useEffect(() => {
    setPool(pools[0])
    if (pools[0]) {
      const { status, start_at, time, timeClose, type } = pools[0]
      if (status === 0) {
        setLeftTime(start_at * 1000 - Date.now())
      } else if (status === 1) {
        if (type === 0) {
          if (now >= timeClose) {
            setLeftTime((time - now) * 1000)
          } else {
            setLeftTime((timeClose - now) * 1000)
          }
        } else {
          setLeftTime((time - now) * 1000)
        }
      }
    }
  }, [pools, address])

  const onClaim = () => {
    const contract = getContract(library, pool.abi, address)
    if (pool.type === 1) {
      contract.methods
        .claim()
        .send({
          from: account,
        })
        .on('transactionHash', (hash) => {
          dispatch({
            type: HANDLE_SHOW_WAITING_WALLET_CONFIRM_MODAL,
            showWaitingWalletConfirmModal: { ...waitingPending, hash },
          })
        })
        .on('receipt', (_, receipt) => {
          console.log('BOT staking success')
          dispatch({
            type: HANDLE_SHOW_WAITING_WALLET_CONFIRM_MODAL,
            showWaitingWalletConfirmModal: waitingForInit,
          })
          dispatch({
            type: HANDLE_SHOW_TRANSACTION_MODAL,
            showTransactionModal: true,
          })
        })
        .on('error', (err, receipt) => {
          console.log('BOT staking error', err)
          dispatch({
            type: HANDLE_SHOW_FAILED_TRANSACTION_MODAL,
            showFailedTransactionModal: true,
          })
          dispatch({
            type: HANDLE_SHOW_WAITING_WALLET_CONFIRM_MODAL,
            showWaitingWalletConfirmModal: waitingForInit,
          })
        })
    } else {
      contract.methods
        .settle()
        .send({
          from: account,
        })
        .on('transactionHash', (hash) => {
          dispatch({
            type: HANDLE_SHOW_WAITING_WALLET_CONFIRM_MODAL,
            showWaitingWalletConfirmModal: { ...waitingPending, hash },
          })
        })
        .on('receipt', (_, receipt) => {
          console.log('BOT staking success')
          dispatch({
            type: HANDLE_SHOW_WAITING_WALLET_CONFIRM_MODAL,
            showWaitingWalletConfirmModal: waitingForInit,
          })
          dispatch({
            type: HANDLE_SHOW_TRANSACTION_MODAL,
            showTransactionModal: true,
          })
        })
        .on('error', (err, receipt) => {
          console.log('BOT staking error', err)
          dispatch({
            type: HANDLE_SHOW_FAILED_TRANSACTION_MODAL,
            showFailedTransactionModal: true,
          })
          dispatch({
            type: HANDLE_SHOW_WAITING_WALLET_CONFIRM_MODAL,
            showWaitingWalletConfirmModal: waitingForInit,
          })
        })
    }
  }
  return (
    <div>
      <PoolsHeader address={address} pool={pool} />
      <div className="pools-container">
        <div className="pools-information">
          <div className="pools-item-container">
            <div className="pools-item-label">Token Distribution</div>
            <div className="pools-item-value">{pool && pool.pool_info.token_distribution}</div>
          </div>

          <div className="pools-item-container">
            <div className="pools-item-label">Allocation</div>
            <div className="pools-item-value">
              {pool && pool.pool_info.min_allocation}-
              {pool && pool.pool_info.max_allocation}
            </div>
          </div>

          <div className="pools-item-container">
            <div className="pools-item-label">Hard Capacity</div>
            <div className="pools-item-value">{pool && pool.pool_info.min_swap_level}</div>
          </div>

          <div className="pools-item-container">
            <div className="pools-item-label">Token Name</div>
            <div className="pools-item-value">{pool && pool.underlying.name}</div>
          </div>

          <div className="pools-item-container">
            <div className="pools-item-label">Address</div>
            <div className="pools-item-value address">{pool && pool.underlying.address}</div>
          </div>

          <div className="pools-item-container">
            <div className="pools-item-label">Total supply</div>
            <div className="pools-item-value">{pool && `${formatAmount(pool.underlying.total_supply, pool.underlying.decimal)} ${pool.underlying.symbol}`}</div>
          </div>

          <div className="pools-item-container">
            <div className="pools-item-label">Ratio</div>
            <div className="pools-item-value">{pool && pool.ratio}</div>
          </div>

          <div className="pools-item-container">
            <div className="pools-item-label">Access</div>
            <div className="pools-item-value private">
              {pool && pool.type === 1 ? 'Private' : ''}
              {pool && (pool.type === 0 || pool.type === 2) ? 'Public' : ''}
            </div>
          </div>

          <div className="pools-sharing">
          {pool.website && pool.website !== '-' && (
              <a
                href={pool.website}
                target="_blank"
                rel="noopener noreferrer"
              >
                <AiOutlineGlobal />
              </a>
            )}
            {pool.twitter && pool.twitter !== '-' && (
              <a
                href={pool.twitter}
                target="_blank"
                rel="noopener noreferrer"
              >
                <FiTwitter />
              </a>
            )}
            {pool.Telegram_Channel && pool.Telegram_Channel !== '-' && (
              <a
                href={pool.Telegram_Channel}
                target="_blank"
                rel="noopener noreferrer"
              >
                <FaTelegramPlane />
              </a>
            )}
            {pool.Github && pool.Github !== '-' && (
              <a
                href={pool.Github}
                target="_blank"
                rel="noopener noreferrer"
              >
                <FiGithub />
              </a>
            )}
          </div>
        </div>
        {/* col2 */}
        <div className="pools-joined">
          <div style={{ paddingTop: 20, paddingBottom: 20 }}>
            <div className={`c100 p${Math.floor(pool.progress > 1 ? 100 : pool.progress * 100)} blue`}>
              <span>
                {pool
                    ? `${pool.progress > 1 ? 100 : pool.progress * 100}%`
                    : '0%'}
              </span>
              <div className="slice">
                <div className="bar" />
                <div className="fill" />
              </div>
            </div>
          </div>

          {pool && pool.type === 1 && (
            <div className="pools-item-container">
              <div className="pools-item-label">Your quota</div>
              <div className="pools-item-value address">{pool && formatAmount(pool.quotaOf)}{' '}{pool && pool.currency.symbol}</div>
            </div>
          )}
          <div className="pools-item-container">
            <div className="pools-item-label">Amount</div>
            <div className="pools-item-value">
              {pool && formatAmount(pool.totalPurchasedUnderlying, pool.underlying.decimal, 2)}/
              {pool && pool.amount} {pool && pool.underlying.symbol}
            </div>
          </div>

          {pool && pool.status === 0 && (
            <div className="pools-item-container">
              <div className="pools-item-label">Status</div>
              <div className="pools-item-value">
                <div className="comming">
                  Comming soon...
                </div>
              </div>
            </div>
          )}
          {pool && pool.status === 1 && (pool.timeClose === 0 || pool.timeClose > now) && (
            <div className="pools-item-container">
              <div className="pools-item-label">Status</div>
              <div className="pools-item-value">
                <div className="comming">
                Undergoing...
                </div>
              </div>
            </div>
          )}
          {pool && pool.status === 1 && pool.timeClose > 0 && pool.timeClose < now && (
            <div className="pools-item-container">
              <div className="pools-item-label">Status</div>
              <div className="pools-item-value">
                <div className="comming">
                  Waiting...
                </div>
              </div>
            </div>
          )}
          {pool && pool.status === 2 && (
            <div className="pools-item-container">
              <div className="pools-item-label">Status</div>
              <div className="pools-item-value">
                <div className="comming">
                Calculating...
                </div>
              </div>
            </div>
          )}
          {pool && pool.status === 3 && (
            <div className="pools-item-container">
              <div className="pools-item-label">Status</div>
              <div className="pools-item-value">
                <div className="comming">
                Finished
                </div>
              </div>
            </div>
          )}

          <div className="pools-buttons">
            <div className="btn-wrapper">
              <a
                className={`pools-btn join ${
                  pool &&
                  pool.status === 1 &&
                  (Number(pool.timeClose) === 0 || pool.timeClose * 1 > now)
                    ? 'active'
                    : 'disabled'
                }`}
                onClick={() => {
                  if (pool.status === 1) {
                    if (pool.type === 1 && pool.purchasedCurrencyOf > 0) {
                      alert(intl.formatMessage({ id: 'alreadySubscribed' }))
                    } else if (pool.type === 1 && pool.quotaOf === 0) {
                      alert(intl.formatMessage({ id: 'notInWhitelist' }))
                    } else {
                      if (pool.timeClose > 0 && pool.timeClose * 1 < now) {
                        alert(intl.formatMessage({ id: 'undergoingOver' }))
                      } else {
                        dispatch({
                          type: HANDLE_WALLET_MODAL,
                          walletModal: 'join',
                          pool,
                        })
                      }
                    }
                  } else {
                    alert(intl.formatMessage({ id: 'cannotSubscribe' }))
                  }
                }}
              >
                <FormattedMessage id='poolsDetailText3' />
              </a>
              <a
                className="pools-btn heco"
                href={`https://${chainId === 97 || chainId === 56 ? 'bscscan.com' : 'hecoinfo.com'}/address/${address}`}
                target='_blank'
                rel="noopener noreferrer"
              >
                View on {chainId === 97 || chainId === 56 ? 'Binance Smart Chain' : 'HECO Chain'}
              </a>
            </div>
          </div>
        </div>
      </div>

      <div className="pools-record">
        <div className='pools_detail_record'>
          {pool &&
            pool.type === 1 &&
            pool.quotaOf == 0 &&
            left_time > 0 &&
            pool.status == 0 && (
              <div className='mask_layer'>
                <p style={{ lineHeight: '30px', marginBottom: '0' }}>
                  <FormattedMessage id='countdown' />
                  &nbsp;
                  <span className='pools-type_time'>
                    <Timer
                      key={left_time}
                      initialTime={left_time}
                      direction='backward'
                      formatValue={(number) => {
                        if (number === 0) return '00'
                        if (number < 10) {
                          return `0${number}`
                        }
                        return number
                      }}
                    >
                      <span>
                        <Timer.Consumer>
                          {({ h, d, formatValue }) => formatValue(d * 24 + h)}
                        </Timer.Consumer>
                      </span>
                      &nbsp;:&nbsp;
                      <span>
                        <Timer.Minutes />
                      </span>
                      &nbsp;:&nbsp;
                      <span>
                        <Timer.Seconds />
                      </span>
                    </Timer>
                  </span>
                </p>
              </div>
            )}
          {pool && pool.type === 1 && pool.quotaOf == 0 && pool.status > 0 && (
            <div className='mask_layer'>
              <p style={{ lineHeight: '30px', marginBottom: '0' }}>
                The user is not in whitelist
              </p>
            </div>
          )}
          <div className='pools_detail_record_tab'>
            <a
              onClick={() => setRecordTab(1)}
              className={cs(recordTab === 1 && 'active')}
            >
              <FormattedMessage id='FundraisingRecord' />
            </a>
            {pool && pool.status >= 1 && (
              <a
                onClick={() => setRecordTab(2)}
                className={cs(recordTab === 2 && 'active')}
              >
                <FormattedMessage id='poolsDetailText5' />
              </a>
            )}
          </div>
          {recordTab === 1 && (
            <div className='pools_detail_record_box'>
              <table className='pools_detail_record_title'>
                <thead>
                  <tr>
                    <td>
                      <FormattedMessage id='invest' />
                      <FormattedMessage id='num' />
                    </td>
                    <td>
                      <FormattedMessage id='winningRate' />
                    </td>
                    <td>
                      <FormattedMessage id='winningAmount' />
                    </td>
                  </tr>
                </thead>
                <tbody>
                  {(pool && pool.purchasedCurrencyOf.toString()) > 0 ? (
                    <tr>
                      <td>
                        {fromWei(pool.purchasedCurrencyOf).toFixed(6, 1) * 1}
                        &nbsp;
                        {pool && pool.currency.symbol}
                      </td>
                      <td>
                        {pool && pool.type === 1 && pool.quotaOf > 0 && (
                          <FormattedMessage id='whiteList' />
                        )}
                        {pool &&
                          pool.type === 0 &&
                          fromWei(pool.settleable.rate)
                            .multipliedBy(new BigNumber(100))
                            .toFixed(2, 1)
                            .toString() *
                            1 +
                            '%'}
                      </td>
                      {/*<td>{Web3.utils.fromWei(pool.settleable.volume, 'ether')}</td>*/}
                      <td>
                        {pool &&
                          pool.type === 0 &&
                          new BigNumber(
                            Web3.utils.fromWei(pool.purchasedCurrencyOf, 'ether')
                          )
                            .multipliedBy(
                              new BigNumber(
                                Web3.utils.fromWei(pool.settleable.rate, 'ether')
                              )
                            )
                            .dividedBy(new BigNumber(pool.price))
                            .toFixed(6, 1)
                            .toString() * 1}
                        {pool &&
                          pool.type === 1 &&
                          formatAmount(pool.settleable.volume)}
                        &nbsp;
                        {pool && pool.underlying.symbol}
                      </td>
                    </tr>
                  ) : (
                    <tr>
                      <td>-</td>
                      <td>-</td>
                      <td>-</td>
                    </tr>
                  )}
                </tbody>
              </table>
            </div>
          )}
          {recordTab === 2 && (
            <div className='pools_detail_record_box'>
              <table className='pools_detail_record_title'>
                <thead>
                  <tr>
                    <td>
                      <FormattedMessage id='unsettlement' />
                    </td>
                    <td>
                      <FormattedMessage id='obtain' />
                      <FormattedMessage id='num' />
                    </td>
                    <td></td>
                  </tr>
                </thead>
                <tbody>
                  {(pool && pool.purchasedCurrencyOf.toString()) > 0 ? (
                    <tr>
                      <td>
                        {pool && formatAmount(pool.settleable.amount)}&nbsp;
                        {pool && pool.currency.symbol}
                        {pool &&
                          pool.settleable.amount > 0 &&
                          pool.status == 1 &&
                          now >= pool.timeClose &&
                          now < pool.time && (
                            <a
                              style={{ marginLeft: '4px' }}
                              className='pools_detail_record_btn'
                              onClick={() => onClaim()}
                            >
                              <FormattedMessage id='poolsDetailText5' />
                            </a>
                          )}
                      </td>
                      <td>
                        {pool && formatAmount(pool.settleable.volume)}&nbsp;
                        {pool && pool.underlying.symbol}
                      </td>
                      <td>
                        {/*  && !pool.settleable.completed_ */}
                        {pool &&
                          pool.type === 0 &&
                          pool.settleable.volume > 0 &&
                          pool.status >= 2 &&
                          now > pool.timeClose &&
                          now >= pool.time && (
                            <a
                              className='pools_detail_record_btn'
                              onClick={() => onClaim()}
                            >
                              <FormattedMessage id='poolsDetailText5' />
                            </a>
                          )}
                        {pool &&
                          pool.type === 1 &&
                          pool.settleable.volume > 0 &&
                          pool.settleable.claimedOf == 0 &&
                          pool.status >= 2 &&
                          now > pool.timeClose &&
                          now >= pool.time && (
                            <a
                              className='pools_detail_record_btn'
                              onClick={() => onClaim()}
                            >
                              <FormattedMessage id='poolsDetailText5' />
                            </a>
                          )}
                      </td>
                    </tr>
                  ) : (
                    <tr>
                      <td>-</td>
                      <td>-</td>
                      <td></td>
                    </tr>
                  )}
                </tbody>
              </table>
            </div>
          )}
        </div>
      </div>
    </div>
  )
}

export default injectIntl(PoolsDetail)
